import { getIconForProperty } from "../../core";

const mdParser = new MarkdownIt();
MdEditor.use(Plugins.AutoResize, {
    min: 100
})
MdEditor.unuse(Plugins.Clear);

/**
 * Render a markdown field that allows edition and seeing the preview.
 *
 * This is one of the internal components that get mapped natively inside forms
 * and tables to the specified properties.
 * @category Form fields
 */
export function MarkdownFieldBinding({
                                         propertyKey,
                                         value,
                                         setValue,
                                         error,
                                         showError,
                                         disabled,
                                         autoFocus,
                                         touched,
                                         property,
                                         tableMode,
                                         includeDescription,
                                         context
                                     }: FieldProps<string>) {

    const [internalValue, setInternalValue] = React.useState(value);
    const valueRef = useRef(value);

    const deferred = useDeferredValue({
        internalValue,
        value
    });

    useEffect(() => {
        valueRef.current = value;
        setInternalValue(value);
    }, [value]);

    useEffect(() => {
        if (deferred.internalValue !== valueRef.current)
            setValue(deferred.internalValue);
    }, [deferred]);

    return (
        <StyledFormControl
            required={property.validation?.required}
            error={showError}
            fullWidth>

            {!tableMode && <FormHelperText filled>
                <LabelWithIcon icon={getIconForProperty(property)}
                               title={property.name}/>
            </FormHelperText>}

            <MdEditor value={internalValue ?? ""}
                      readOnly={disabled}
                      renderHTML={text => mdParser.render(text)}
                      view={{
                          menu: true,
                          md: true,
                          html: false
                      }}
                      onChange={({
                                     html,
                                     text
                                 }) => {
                          setInternalValue(text ?? null);
                      }}/>

            <Box display={"flex"}>
                <Box flexGrow={1}>
                    {showError &&
                        typeof error === "string" &&
                        <FormHelperText error={true}>{error}</FormHelperText>}
                    {includeDescription &&
                        <FieldDescription property={property}/>}
                </Box>
            </Box>

        </StyledFormControl>
    );

}

